
:root{
  --primari-font: "Roboto", sans-serif;
  --primari-font-weight: 500;
  --primari-text-color: #000000;
  --secondary-text-color: #8C8C8C;

}

h1,
h2,
h3,
h4,
p{
  margin-top: 0;
  margin-bottom: 0;
  
}
ul,
ol{
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}
img{
  display: block;
  
}

button{
  cursor: pointer;
}
.list-modal-items{
  list-style: none;
}
.modal-film-card{
  // position: absolute;
  //   left: 50%;
  //   top: 50%;
  //   transform: translate(-50%, -50%);
  width: 280px;
    height: 940px;
    left: 20px;
    top: 20px;
  
    background: #FFFFFF;
    @media screen and (min-width: 768px) {
      display: flex;
      width: 704px;
      height: 552px;
    }
    @media screen and (min-width: 1200px) {
          
          width: 806px;
          height: 558px;
    }
}

.modal-card-film-close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;

  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.1);
  transition: var(--transition-dur-and-func);

  &:hover,
  &:focus {
    border: 1px solid var(--accent-color);
  }
}



.modal-card-film-close-btn-icon {
  z-index: 999;
  fill: #000000;
  transition: var(--transition-dur-and-func);

  .modal-franchise-close-btn:hover &,
  .modal-franchise-close-btn:focus & {
    fill: var(--accent-color);
  }
}

.img-card-modal {
  
  width: 240px;
  height: 357px;
  padding-top: 40px;
  padding-right: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  border-radius: 5px;

  @media screen and (min-width: 768px) {
    width: 264px;
    height: 373.55px;
    padding-top: 40px;
    padding-right: 68px;
    padding-bottom: 138.5px;
    padding-left: 36px;
    border-radius: 5px;
  }

  @media screen and (min-width: 1200px) {
    width: 375px;
    height: 478px;
    padding-top: 40px;
    padding-right: 16px;
    padding-bottom: 40px;
    padding-left: 12px;
    border-radius: 5px;
  }
}
.name-film{
  // position: absolute;
  //   width: 266px;
  //   height: 31px;
  //   left: 640px;
  //   top: 190px;
  margin: 20px;
    font-family: var(--primari-font);
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
      line-height: 1.15;
  
    color: var(--primari-text-color);
    @media screen and (min-width: 768px) {
      margin-left: 0px;
        margin-top: 40px;
        margin-right: 72px;
        margin-bottom: 20px;
        
      }
    @media screen and (min-width: 1200px) {
      
        margin-right: 137px;
       
        font-size: 30px;
        line-height: 35px;
      }

}

.box-film-params{
  display: flex;
}

.list-modal{
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding-right: 45px;
  padding-left: 20px;
}
.list-modal-items__params{
  display: flex;
  // flex-direction: column;
  // gap: 8px;
  // justify-content: space-between;
  font-family: var(--primari-font);
    font-style: normal;
    font-weight: var(--primari-font-weight);
    font-size: 12px;
    line-height: 1,33;
    color: var(--secondary-text-color);
}
.list-modal-items__params-value{
  font-family: var(--primari-font);
    font-style: normal;
    font-weight: var(--primari-font-weight);
    font-size: 12px;
    line-height: 1.17;
    /* identical to box height */
  
  
  
    color: var(--primari-text-color);
}
.dеscription__params{
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding-left: 20px;
  margin-top: 20px;
  // margin-bottom: 8px;
  font-family: var(--primari-font);
    font-style: normal;
    font-weight: var(--primari-font-weight);
    font-size: 12px;
    line-height: 1.33;
    /* identical to box height, or 133% */
  
   color: var(--primari-text-color);
  @media screen and (min-width: 768px) {
      padding-left: 0px;
  
    }
  
 
}
.items__value{
  
  
  font-family: var(--primari-font);
    font-style: normal;
    font-weight: var(--primari-font-weight);
    font-size: 12px;
    line-height: 1.66;
    /* or 167% */
  
  
    color: var(--primari-text-color);
}

.box-modal-btn{
  margin-top: 20px;
  margin-right: 20px;
  margin-bottom: 40px;
  margin-left: 20px;
  display: flex;
  gap: 20px;
  @media screen and (min-width: 768px) {
      margin-left: 0px;
      gap: 15px;
  
    }
}

.modal-submint-btn{
  margin-top: 20px;
  margin-bottom: 40px;
  width: 110px;
  height: 44px;

  font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    /* or 133% */
  
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    text-transform: uppercase;
    border-radius: 5px;
  
    color: var(--primari-text-color);
    background: #FFFFFF;
    &:hover{
      background: #FF6B01;
      color: #FFFFFF;
    }
    &:focus{
      background: #FF6B01;
      color: #FFFFFF;
    }
        @media screen and (min-width: 768px) {
          width: 125px;
        }
        @media screen and (min-width: 1200px) {
          width: 136px;
        }
}

















